html, body, #root {
    height: 100%;
    margin: 0;
    padding: 0;
}
.first-screen {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.header-container {
    height: auto;
}
.image {
    display: block;
}
.slider-container{
    height: auto;
}
.cards-container{
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    height: auto;
    width: 100%;
}


.second-screen {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 2rem;
    padding: 2rem;
    flex-wrap: wrap; /* ëª¨ë°”ì¼ ëŒ€ì‘ */
  
}

.map-container {
    height: 80vh;
}

.list-button {
    padding-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
  
.list-button button {
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    background-color: #6d6d6e;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
  
.list-button button:hover {
    background-color: #005fcc;
}


